<template>
  <div class="qdao">
    <van-nav-bar
      title="每日签到"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <div class="qdimg"></div>
    <van-button type="info" class="btn" round @click="ann">每日签到</van-button>
    <van-calendar
      v-model="show"
      :min-date="minDate"
      @confirm="confirm"
      @select="select"
      :color="col"
      type="multiple"
    />

    <div class="img-box">
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      minDate: new Date(),
      maxDate: new Date(2020, 12, 31),
      col: "#99f1ce",
    };
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    ann() {
      this.show = true;
    },
    confirm() {
      this.show = false;
    },
    select() {
      this.$toast.success("签到成功");
      this.col = "#49dda1";
    },
  },
};
</script>

<style lang="scss" scoped>
.qdao {

  .qdimg {
      position: relative;
    margin-top: 46px;
    width: 100vw;
    height: 150px;
    background-image: url("http://img.manhua.weibo.com/hcover/2018/09/07/2084322240_aOFgE7Cs.jpg");
    filter: blur(20px);
  }
  button {
      position: absolute;
      top: 50px;
      left: 0;
  }
  .btn {
    border: none;
    background-color: #49dda1;
    margin-top: 100px;
    margin-left: 24%;
  }
  /deep/ .van-nav-bar .van-icon {
    color: #49dda1;
  }
  /deep/.van-nav-bar__text {
    color: #49dda1;
  }
  /deep/.van-button--normal {
    padding: 0px 66px;
    font-size: 14px;
  }
  
  .img-box{
    width: 100%;
    height: 200px;
    background-image: url('../assets/images/qdao.png');
    background-repeat: no-repeat;
    background-size: 372px;
    
  }
}
</style>